/* Utilities
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.tal {
 text-align: left; }
.tac {
 text-align: center;
 margin-left: auto;
 margin-right: auto; }
.tar {
 text-align: right; }
.taj {
 text-align: justify; }
.mb-1 {
 margin-bottom: -1rem; }

.block {
 display: block; }

.sunset {
 color: $sunset; }
.sunshine {
 color: $sunshine; }
.fresh {
 color: $fresh; }
.sea {
 color: $sea; }
.sun {
 color: $sun; }

.monospace {
 @include font(monospace, false, false, false, false); }
.fantasy {
 @include font(fantasy, false, false, false, false); }
.cursive {
 @include font(cursive, false, false, false, false); }
.japanese {
 @include font(false, false, false, false, false); }
.clearfix {
 @include clearfix; }

/* For grids
 ** –––––––––––––––––––––––––––––––––––––––––––––––––– */
.container:after, .row:after {
 content: "";
 display: table;
 clear: both; }
