/* arrow up
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.top {
 position: fixed;
 bottom: 90px;
 right: -55px;
 background-color: #dfdce3;
 border-radius: 0.3rem;
 fill: #FFFFFF;
 background-color: rgba(230, 46, 104, 0.7);
 cursor: pointer;
 transition: all .25s ease;
 z-index: 10;
 &.up {
 right: 30px; }
 &:hover {
 background-color: rgba(230, 46, 104, 1); } }
