a {
 background-color: transparent;
 -webkit-text-decoration-skip: objects;
 @include font(sans-serif, false, false, 600, false);
 text-decoration: none;
 &:hover {}
 &:focus,
 &:active,
 &:visited {
 @include font(false,false,false,false,false);
 text-decoration: none;
 outline-width: 0; } }
