/* Media
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

img,
video,
object {
 max-width: 100%;
 height: auto;
 border: none;
 outline: none; }

figure {
 max-width: 100%;
 margin: auto; }

figcaption {
 margin: 2rem auto;
 text-align: center;
 @include font(false, false, false, false, false); }

.bgkImage {
 background-repeat: no-repeat;
 background-size: cover;
 background-position: 50% 50%;
 background-image: url(data:image/jpeg;base64,);
 } //background-blend-mode: lighter
@include mediaQ(medium) {
 .bgkImage {
 background-image: url(#{$imageSplash}.jpg); } }


.replace {
 background-image: url(data:image/jpeg;base64,); }
