/* Privacy Cookies Button
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.sectionPrivacy {
 z-index: 999; }

#idPrivacyCookie {
 display: none;
 position: fixed;
 padding: 10px 30px;
 width: 100%;
 bottom: 0;
 background-color: rgba(38, 57, 89, 0.6);
 color: #ffffff;
 text-align: center;
 a {
 display: inline-block;
 cursor: pointer; } }

%btnPriv {
 color: #FFFFFF;
 width: 100px;
 border: 1px solid #FF0025;
 background-color: #FF0025;
 border-radius: 5px;
 padding: 4px 16px;
 margin: 10px 0;
 &:hover {
 background-color: #dddddd; } }


.btnPrivacy {
 @extend %btnPriv;
 margin-left: 15px;
 &:visited {
 color: #FFFFFF; }
 &:hover {
 color: #FF0025; } }

.btnPrivacyCookies {
 @extend %btnPriv;
 margin-right: 15px;
 transition: background-color .35s ease-in-out;
 &:hover {
 color: #263959; } }
