/* Mixins
 ** –––––––––––––––––––––––––––––––––––––––––––––––––– */

@mixin clearfix {
 &:after {
 content: "";
 display: table;
 clear: both; } }

@mixin font($ff: false, $size: false, $colour: false, $weight: false, $lh: false) {
 @if $ff {
 font-family: $ff; }
 @if $size {
 font-size: $size; }
 @if $colour {
 color: $colour; }
 @if $weight {
 font-weight: $weight; }
 @if $lh {
 line-height: $lh; } }

@mixin backgroundImage($color: false, $blend: false) {
 background: {
 repeat: no-repeat;
 size: cover;
 position: 50% 50%;
 @if $color {
 color: $color; }
 @if $blend {
 blend-mode: $blend; } } }

@mixin position($position, $top: null, $right: null, $bottom: null, $left: null) {
 position: $position;
 top: $top;
 right: $right;
 bottom: $bottom;
 left: $left; }

@mixin size($size: null) {
 width: $size;
 height: $size; }

@mixin mediaQ($val) {
 @if $val == exsmall {
 @media all and (min-width: 551px) {
 @content; } }
 @else if $val == small {
 @media all and (min-width: 768px) {
 @content; } }
 @else if $val == medium {
 @media all and (min-width: 1025px) {
 @content; } }
 @else if $val == large {
 @media all and (min-width: 1224px) {
 @content; } }
 @else {
 @media all and (min-width: $val) {
 @content; } } }

@mixin cv {
 position: absolute;
 top: 50%;
 left: 50%;
 transform: translate(-50%, -50%); }

