/* Footer
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.siteFooter {
 background-color: transparent;
 width: 100%;
 //min-height: 100px
 @include position(fixed,null,null,0px,null); }


.wrapperFooter {
 display: table-cell;
 vertical-align: middle;
 border: 1rem solid transparent;
 //+font(false,false,false,false,60px)
 margin: 0 auto;
 text-align: center;

 .button {
 margin: 0.8rem;
 background-color: rgba(230, 46, 104, 0.7); }
 .button:hover, button:hover {
 border-bottom-color: transparent;
 background-color: rgba(230, 46, 104, 1); } }

.footerStrip {
 //width: 100%
 height: 3.6rem;
 background-color: rgba(38, 57, 89, 0.6);
 span {
 @include font(false, 1.2rem,#FFFFFF,600,3.6); }
 a {
 img {
 vertical-align: middle;
 line-height: 36px;
 @include size(3rem);
 display: inline;
 margin: 0 2rem 0 1rem; } } }
