/* Base styles
 ** Table of contens:
 *
 *- background-color: rgba(255,235,39,1)
 *- background-color: rgba(38,57,89,0.6)
 *- background-color: rgba(230, 46, 104, 0.7)
 *- color: rgba(230, 46, 104, 1)
 *- border-bottom-color: #FFFFFF
 *- box-shadow: 0px 3px 10px rgba(0,0,0,0.2)
 *
 *- NOTE: html is set to 62.5% so that all the REM measurements throughout Skeleton
 *- are based on 10px sizing. So basically 1.5rem = 15px :)
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

$grey050: #fafafa;
$grey500: #9e9e9e;
$grey700: #616161;
$white : #ffffff;
$black : #000000;
$text: #222222;
$clean: #dfdce3;

$sunset: rgba(230, 46, 104, 1);
$sunshine: rgba(230, 110, 46, 1);
$fresh: rgba(200, 231, 179, 1);
$sea: rgba(147, 219, 244, 1);
$sun: rgba(255, 235, 39, 1);

//$systemFF: Raleway, HelveticaNeue, Helvetica, Arial, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif
$systemFF: sans-serif, serif, monospace, fantasy, cursive;

*,
*::before,
*::after {
 box-sizing: border-box;
 box-sizing: inherit; }

html {
 -webkit-text-size-adjust: 100%;
 -ms-text-size-adjust: 100%;
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
 font-variant-ligatures: common-ligatures;
 -webkit-tap-highlight-color: transparent;
 font-kerning: normal;
 font-family: $systemFF;
 font-size: 62.5%;
 box-sizing: border-box; }

html.no-js {
 body:before {
 position: absolute;
 top: 50%;
 left: 50%;
 transform: translate(-50%, -50%);
 content: "Contact me at rotokyo@icloud.com"; } }

body {
 -webkit-text-size-adjust: 100%;
 -webkit-font-smoothing: antialiased;
 font-kerning: normal;
 font-variant-ligatures: common-ligatures;
 text-rendering: optimizeLegibility;
 overflow-x: hidden;
 /* currently ems cause chrome bug misinterpreting rems on body element */
 @include font($systemFF,1.6rem,#222222,400,1.6);
 margin: 0; }

/* margins
 *–––––––––––––––––––––––––––––––––––––––––––––––––– */

input, textarea, select, fieldset {
 margin-bottom: 1.5rem; }

pre, blockquote, dl, figure, table, p, ul, ol, form {
 margin-bottom: 2.5rem; }

/* hr
 *–––––––––––––––––––––––––––––––––––––––––––––––––– */

hr {
 margin: 3rem auto 4rem;
 max-width: 70%;
 border : 0;
 height: 1px;
 background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0)); }
