/* Section Hero
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.sectionHero {
 height: 100%;
 text-align: center;
 margin: 0 auto 150px;
 position: relative; }

.wrapperBanner {
 @include size(100%);
 @include position(fixed,null,null,100px,null);
 text-align: center;
 margin: 0 auto;
 height: 30rem; }

@include mediaQ(exsmall) {
 .wrapperBanner {
 bottom: 70px; } }
@include mediaQ(large) {
 .wrapperBanner {
 bottom: 30px; } }

.wrapperTextBanner, .wrapperText {
 padding: 35px 0;
 background-image: radial-gradient(40% 55%, rgb(226,217,200), transparent); }

h6 {
 @include font(false,false,false,false,false);
 text-transform: uppercase;
 letter-spacing: 5px;
 font-variant: small-caps; }
