/* Tipography
 ** Table of contens:
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

%textJ {
 text-align: justify;
 word-wrap: break-word;
 word-break: break-all;
 hyphens: auto; }

%text {
 text-align: justify;
 word-break: keep-all;
 hyphens: auto; }

%titoli {
 text-transform: uppercase;
 text-align: center;
 white-space: nowrap; }

h1, h2, h3, h4, h5, h6 {
 margin-top: 0;
 margin-bottom: 3rem;
 @include font(monospace, false, false, 600, false);
 word-break: keep-all;
 hyphens: auto;
 } //white-space: nowrap

h1 {
 @include font(false, 4.0rem, false, false, 1.2);
 letter-spacing: .4rem;
 white-space: normal; }

h2 {
 @include font(false, 3.6rem, false, false, 1.25);
 } //letter-spacing: -.1rem

h3 {
 @include font(false, 3.0rem, false, false, 1.3);
 } //letter-spacing: -.1rem

h4 {
 @include font(false, 2.4rem, false, false, 1.35);
 } //letter-spacing: -.08rem

h5 {
 @include font(false, 1.8rem, false, false, 1.5);
 } //letter-spacing: -.05rem

h6 {
 @include font(false, 1.5rem, false, false, 1.6);
 letter-spacing: 0; }

/*
 *- NOTE: scala aurea
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */
@include mediaQ(exsmall) {
 h1 {
 @include font(false, 5.0rem, false, false, 1.1); }
 h2 {
 @include font(false, 4.2rem, false, false, 1.18); }
 h3 {
 @include font(false, 3.1rem, false, false, 1.34); }
 h4 {
 @include font(false, 2.6rem, false, false, 1.19); }
 h5 {
 @include font(false, 1.9rem, false, false, 1.36); }
 h6 {
 @include font(false, 1.6rem, false, false, 1.19); } }


p {
 /* from main: +font($systemFF,1.6rem,#222222,400,1.6) */
 @include font(false, false, false, false, false);
 margin-top: 0;
 hyphens: auto; }

@include mediaQ(exsmall) {
 p {
 @include font(false, 1.8rem, false, false, 1.8); } }

/* Tipography
 ** Table of contens:
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.classTitle {
 @extend %titoli;
 @include font(false,false,rgba(230, 46, 104, 1),700,false);
 letter-spacing: 1rem;
 padding: 7rem 0 0; }

.it {
 display: none; }
.en {
 display: none; }
.jp {
 display: none; }
